.guides {
  display: "block";
  cursor: grab;
  pointer-events: all;
  z-index: 999990;
}
.guides:hover {
  cursor: grab;
  pointer-events: all;
}

.guides_pseudoEL {
  position: absolute;
  content: "";
  z-index: 10000;
  display: inline-block;
  height: 0;
  width: 0;
  display: block;
  cursor: hand;
  z-index: 999990;
}

.UI_guides__container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #34495e;
  position: fixed;
  width: 100px;
  height: 50px;
  bottom: 0;
  right: 0;
  border-top-left-radius: 5px;
  padding: 0 1em;
}
.UI_guides__container label {
  color: white;
  font-size: 0.75em;
  margin: 0 1em 0 0;
  text-align: center;
}
.UI_guides__container input {
  border-radius: 0;
  background: transparent;
  width: 30px;
  padding: 0 8px;
  text-align: center;
  background-color: white;
  border: 1px solid black;
  border-radius: 3px;
  pointer-events: all;
}

.contextMenu {
  position: fixed;
  border-radius: 3px;
  border: 1px solid black;
  background: white;
  z-index: 999998;
  pointer-events: all;
  cursor: grab;
}
.contextMenu header {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  background: #3b4563;
  padding: 0.25em 0.5em 0.5em;
  display: flex;
  box-sizing: border-box;
  cursor: grab;
}
.contextMenu header label {
  color: white;
  font-size: 1em;
  margin-left: 0.5em;
  cursor: grab;
}
.contextMenu header .close_contextMenu {
  width: 17px;
  height: 17px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: space-between;
  cursor: pointer;
  margin-right: 0.25em;
  transition: 0.3s;
}
.contextMenu header .close_contextMenu:hover {
  background-color: red;
  transform: rotate(180deg);
}
.contextMenu header .close_contextMenu span:nth-child(1n) {
  transform: rotate(45deg);
  top: 7px;
  left: 1px;
}
.contextMenu header .close_contextMenu span:nth-child(2n) {
  transform: rotate(-45deg);
  top: 7px;
  right: 1px;
}
.contextMenu header .close_contextMenu .cross {
  width: 15px;
  height: 3px;
  background: black;
  position: absolute;
  transform-origin: center;
  content: " ";
  background: white;
}
.contextMenu_content {
  padding: 0.5em 0.5em 0.5em 0.5em;
}
.contextMenu_blocInput {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.contextMenu_blocInput label {
  margin: 0 0 0 1.25em;
  text-align: center;
  width: 24px;
  height: 28px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}
.contextMenu_blocInput input {
  flex: 1;
  text-align: center;
  max-width: 73px;
  box-sizing: border-box;
}
.contextMenu_blocInput input:hover {
  border: 1px solid #ff8800;
  border-radius: 3px;
}
.contextMenu_blocInput input:focus {
  border: 1px solid #ff8800;
  border-radius: 3px;
}
.contextMenu_blocInput [type=number] {
  text-indent: 0.875em;
}
.contextMenu_blocInput svg {
  width: 1.5em;
  height: 1.5em;
}
.contextMenu hr {
  margin: 0.5rem 0;
}