/* Large desktop */
.UI_body {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999999;
  pointer-events: none;
}
.UI_body .container {
  position: fixed;
  top: calc(50% - 98px);
  left: 16px;
  width: 2.75em;
  border-radius: 3px;
  border: 1px solid black;
  pointer-events: all;
  background-color: white;
}
.UI_body .container header {
  content: "";
  width: 100%;
  height: 16px;
  background-color: #586280;
  cursor: grab;
}
.UI_body .container .menu_content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0.5em 0 0.5em 0;
}
.UI_body .container .menu_content li {
  width: 2em;
  height: 2em;
  background-color: white;
  text-align: center;
  margin: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #cacaca;
  color: white;
  font-weight: bold;
  border-radius: 3px;
}
.UI_body .container .menu_content li img {
  width: 1.6rem;
  height: 1.6rem;
  color: red;
}
.UI_body .container .menu_content li.active {
  border: 2px solid red;
  color: black;
  background: white;
}